<template>
  <div>
    <article>
      <main id="mdHtml"></main>
    </article>
  </div>
</template>

<script>
export default {
  name: "arcitleDeltaie",
  async mounted() {
    // console.log(this.$route.params.id);//params中存储所有动态路由的变量
    const id = this.$route.params.id; //得到动态路由id值
    let { data } = await this.$axios({
      method: "GET",
      url: "/getdata/articleDtailes",
      params: { id },
    });
    // console.log(id);
    if(data.code) {return this.$message.error("你查阅的文章不存在！");
    this.$router.replace("/actire");
        // this.$router.replace("/actire");//未查询到文章后重定向路由
        //发起请求获得md文件
    }else{
    let mdData = await this.$axios({
        method:"GET",
        url:data.data.mdUrl, 
    })
    editormd.markdownToHTML("mdHtml",{
        markdown:mdData.data//文件数据
    });
    }


  },
};
</script>

